import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/services/data.service';
import * as moment from 'moment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-loginactivity',
  templateUrl: './loginactivity.page.html',
  styleUrls: ['./loginactivity.page.scss'],
})
export class LoginactivityPage implements OnInit {
  title: string = 'Login Activity';
  passwordChangeDate: Date;
  currentUser: any;

  constructor(private router: Router,
    private dataService: DataService) { }

  ngOnInit() {
    this.currentUser = this.dataService.getCurrentUser();
    console.log("CurrentUSer?", this.currentUser);
  }

  goToLogin() {
    this.router.navigate(['/securitycenter']);
  }

  formatDateTo(dateParam: any) {
    const date = moment(dateParam);
    let dateTo = date.format('DD/MM/YYYY');
    return dateTo;
  }

  formatDateTimeTo(dateParam: any) {
    const date = moment(dateParam);
    let time = date.format('DD/MM/YYYY | HH:mmA');
    return time;
  }

}
